import React from 'react';
import classNames from 'classnames';
import { DialogOverlay } from '@reach/dialog';

export type ModalOverlayProps = {
  /** Flagg som sier om modalen er pen */
  open?: boolean;
  /** Callback som kalles nr brukeren ber om  lukke modalen */
  onDismiss?: () => void;
  /** Innholdet i modalen */
  children: React.ReactNode;
  /** Ekstra klassenavn */
  className?: string;
  /** En ref til elementet som skal vre fokusert nr modalen pnes. Defaulter til lukkeknappen */
  initialFocusRef?: React.RefObject<HTMLElement>;
  [key: string]: any;
};

export const ModalOverlay: React.FC<ModalOverlayProps> = ({
  className,
  open,
  ...rest
}) => (
  <DialogOverlay
    className={classNames('eds-modal__overlay', className)}
    isOpen={open}
    {...rest}
  />
);
